import React from 'react';
import { InertiaLink, usePage } from '@inertiajs/inertia-react';
import Layout from '@/Components/Layout2';
import Icon from '@/Shared/Icon';
import SearchFilter from '@/Components/SearchFilter';
import Pagination from '@/Components/Pagination';
import DataTable from 'react-data-table-component'

const Index = () => {
  const { organizations } = usePage().props;
  const {
    data,
  } = organizations;
  const columns = [
    {
        name: 'Name',
        selector: row => row.name,
    },
    {
        name: 'City',
        selector: row => row.city,
    },
    {
      name: 'Phone',
      selector: row => row.phone,
    },
  ]
  return (
    <div>
      <div className="border-b border-gray-200 px-4 py-4 sm:flex sm:items-center sm:justify-between sm:px-6 lg:px-8">
        <div className="flex-1 min-w-0 py-2">
          <h1 className="text-lg font-medium leading-6 text-gray-900 sm:truncate">Organizations</h1>
        </div>
        <div className="mt-4 flex sm:mt-0 sm:ml-4">
          <InertiaLink
            className="order-0 items-center px-4 py-2 border border-transparent shadow-sm text-sm font-medium rounded-md text-white bg-yellow-600 hover:bg-yellow-700 focus:outline-none focus:ring-2 focus:ring-offset-2 focus:ring-yellow-500 sm:order-1 sm:ml-3"
            href={route('organizations.create')}
          >
            <span>Create</span>
          </InertiaLink>
        </div>
      </div>
      <div className="overflow-x-auto bg-white rounded shadow">
        <DataTable
          columns={columns}
          data={organizations}
          selectableRows
          pagination
        />
      </div>
    </div>
  );
};

Index.layout = page => <Layout title="Organizations" children={page} />;

export default Index;
